import Typography from '@material-ui/core/Typography';
import React from 'react';
import { LinearProgress, Grid, Container } from '@material-ui/core';
import axios from 'axios';
import * as utilities from '../../utils/utilities';
import * as queries from '../../utils/queries';
import * as _ from 'lodash';
import * as CHART_COLORS from '../../utils/chartcolors';
import { ResponsiveContainer, Brush, LineChart, Line, Legend, XAxis, YAxis, Tooltip, CartesianGrid } from 'recharts';

export default class AreaExpansionPanelDetail extends React.Component<{ area?: string, areaCode?: string }> {

    state = {
        ready: false,
        data_total_cases: []
    }

    constructor(props) {

        super(props);
        this.state = {
            ready: false,
            data_total_cases: []
        }
    }

    componentDidMount() {
        axios.get(utilities.createbaseURLWithQuery(queries.QUERY_TOTAL_CASES_BY_AREA))
            .then(response => {
                let _build_data = [];
                // hyrate array of JSON object with rows based on the columns
                _.map(response.data.rows, function (r) {
                    _build_data.push(_.mapKeys(r, function (v, k) {
                        return response.data.columns[k];
                    }))
                });

                this.setState({ data_total_cases: _build_data });
            })
    }

    render() {
        return this.state.ready ?
            <Container>
                <Grid>
                    <Typography variant="h5">Population: 122,124</Typography>
                    <ResponsiveContainer width='100%' height={500}>
                        <LineChart data={this.state.data_total_cases}>
                            <Line type="monotone" fill={CHART_COLORS.COLOR_PBI_DEFAULT[0]} dataKey="TotalCases" name="Total Cases" />
                            <Legend />
                            <Tooltip />
                            <CartesianGrid strokeDasharray="5 5" />
                            <Brush dataKey="date" height={30} stroke="#8884d8" />
                            <YAxis />
                            <XAxis
                                dataKey="date"
                            // allowDuplicatedCategory={false}
                            // tickFormatter={utilities.formatDateLabel}
                            />
                        </LineChart>
                    </ResponsiveContainer>
                </Grid>
            </Container> : <LinearProgress />
    }
}